<template>
  <div>
    <ul ref="rlist" class="chatting_list">
      <li v-for="msg in getMessages" :key="msg.index">
        <div :class="!!msg.isMine && msg_right">
          <div class="msg_item" :class="msg.isMine ? msg_mine : msg_other">
            <div class="msg_top">
              <div class="msg_user">
                {{ msg.user }}
              </div>
              <div class="msg_time">
                {{ getFormatDate(msg.time) }}
              </div>
            </div>
            <div class="msg_contents">
              {{ msg.message }}
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { EventBus } from "../utils/events-bus";

export default {
  data() {
    return {
      msg_right: "msg_right",
      msg_mine: "msg_mine",
      msg_other: "msg_other"
    };
  },
  computed: {
    ...mapGetters(["getMessages"])
  },
  created() {
    EventBus.$on("updateList", () => {
      this.scrollToBottom();
    });
  },
  mounted() {
    const rlist = this.$refs.rlist;
    const h = window.innerHeight - rlist.getBoundingClientRect().y - 50;
    rlist.style.height = h + "px";

    this.LOAD_MESSAGES().then(() => {
      console.log("load all messsage");
      this.scrollToBottom();

      this.SET_MESSAGE_EVENT();
    });
  },
  beforeDestroy() {
    this.DELETE_MESSAGE_EVENT()
  },
  watch: {
    getMessages() {
      console.log("add item");
      this.$nextTick(function() {
        this.scrollToBottom();
      });
    }
  },
  methods: {
    ...mapActions(["LOAD_MESSAGES", "SET_MESSAGE_EVENT", "DELETE_MESSAGE_EVENT"]),
    getFormatDate(timestamp) {
      const date = new Date(timestamp);
      return date.toLocaleString("en-US", {
        hour: "numeric",
        minute: "numeric",
        hour12: true
      });
    },
    scrollToBottom() {
      const rlist = this.$refs.rlist;
      rlist.scrollTop = rlist.scrollHeight;
    }
  }
};
</script>

<style scoped>
.chatting_list {
  width: 100%;
  overflow-y: scroll;
}

.msg_item {
  box-sizing: border-box;
  margin: 10px;
  padding: 10px;
  width: fit-content;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  position: relative;
  height: 1%;
  border: 0.05px solid rgba(211, 211, 211, 0.5);
}

.msg_top {
  font-weight: bold;
  font-size: 15px;
  display: flex;
  margin-bottom: 5px;
}

.msg_time {
  margin-left: 10px;
  color: darkgray;
  font-weight: normal;
}

* {
  font-family: "Nanum Gothic", sans-serif;
}
.msg_contents {
  font-size: 15px;
}

.chat_text {
  font-size: 20px;
}

.msg_right {
  display: flex;
  justify-content: flex-end;
  text-align: right;
}

.msg_mine {
  background-color: #fff9d9;
}

.msg_other {
  background-color: #f5f5f5;
}
</style>