<template>
  <div class="indicator_bg">
    <div v-for="tab in this.tab_items.length" :key="tab.index">
      <span class="dot" :class="getDotClass(tab)"></span>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { EventBus } from "../utils/events-bus";

export default {
  data() {
    return {
      selected_index: 0
    };
  },
  computed: {
    ...mapState(["tab_items"])
  },
  created() {
    EventBus.$on("selectTab", index => {
      this.selected_index = index;
    });
  },
  methods: {
    getDotClass(index) {
      return index - 1 === this.selected_index ? "selected" : "unselected";
    }
  }
};
</script>


<style scoped>
.indicator_bg {
  width: 100%;
  display: flex;
  justify-content: center;
  /* background-color: yellow; */
  margin: 0;
}

.dot {
  margin-left: 8px;
  margin-right: 8px;
  height: 8px;
  width: 8px;
  border-radius: 50%;
  display: inline-block;
}

.selected {
  background-color: #bfc2c8;
}

.unselected {
  background-color: #e1e1e1;
}
</style>