<template>
  <div>
    <video id="hls-example" class="video-js vjs-default-skin vjs-16-9" controls>
      <source
        ref="video_source"
        type="application/x-mpegURL"
      />
    </video>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      window_width: 100,
      window_height: 50
    };
  },
  computed: {
    ...mapGetters(["getHlsUrl"])
  },
  created() {
    this.$store.dispatch("LOAD_HLS_URL").then(() => {
      this.$refs.video_source.setAttribute("src", this.getHlsUrl)

      window.videojs("hls-example");
    });
  },
  methods: {
    playVideo() {}
  }
};
</script>

<style scoped>
#hls-example {
  background-color: black;
  width: 100vw;
}
</style>