<template>
  <div ref="bg">
    <div ref="items" class="itmes">
      <div ref="glist" class="grid">
        <v-row>
          <v-col v-for="n in emoji_list" :key="n" cols="4">
            <v-img class="shadow" @click="addHeart(n)" aspect-ratio="1">
              <div class="font">{{ n }}</div>
            </v-img>
          </v-col>
        </v-row>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  props: ["props_height"],
  data() {
    return {
      imgs: [],
      timer: null,
    };
  },
  computed: {
    ...mapState(["emoji_list"])
  },
  mounted() {
    const glist = this.$refs.glist;
    const h = window.innerHeight - glist.getBoundingClientRect().y;
    glist.style.height = h + "px";

    this.$refs.bg.style.height = this.props_height + "px";
    this.$refs.items.style.height = this.props_height + "px";
    console.log(this.props_height);
  },
  methods: {
    ...mapActions(["ADD_EMOJI"]),
    addHeart(index) {
      this.ADD_EMOJI(index);
    },
  }
};
</script>

<style scoped>
.grid {
  overflow-y: scroll;
  padding: 20px;
  display: flex;
  position: absolute;
  margin-top: -20px;
  width: 100%;
}

.itmes {
  /* background-color: chocolate; */
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.count {
  opacity: 0;
}
.font {
  left: 0;
  right: 0;
  position: absolute;
  user-select: none;
  font-size: 40px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.shadow {
  border-radius: 20px;
  box-shadow: 1px 1px 4px 1px lightgrey;
}
</style>