<template>
  <div>
    <v-container fluid>
      <h1>CMS</h1>
      <v-row>
        <v-col cols="2">
          <v-subheader>HLS URL :</v-subheader>
        </v-col>
        <v-col cols="8">
          <v-text-field label="URL" v-model="hls_url"></v-text-field>
        </v-col>
        <v-col cols="2">
          <v-btn icon outlined @click="updateHlsUrl">
            <v-icon>mdi-pencil</v-icon>
          </v-btn>
          <v-btn icon outlined class="ml-4" @click="updateMessages">
            <v-icon>mdi-refresh</v-icon>
          </v-btn>
        </v-col>
      </v-row>

      <v-row>
        <v-col>
          <v-data-table
            :headers="headers"
            :items="computed_messages"
            :items-per-page="20"
            class="elevation-1"
          ></v-data-table>
        </v-col>
      </v-row>

      <!-- <v-row>
        <v-col>
          <v-textarea class="mt-5" outlined label="Text"></v-textarea>
        </v-col>
      </v-row> -->
    </v-container>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from "vuex";
import * as FB from "../api/firebase";

export default {
  data() {
    return {
      hls_url: "",
      computed_messages: [],
      headers: [
        {
          text: "Time",
          align: "start",
          sortable: false,
          value: "date"
        },
        { text: "Type", value: 'type' },
        { text: "Message", value: "message" },
        { text: "User", value: "user" }
      ]
    };
  },
  computed: {
    ...mapState(["messages"]),
    ...mapGetters(["getHlsUrl"])
  },
  created() {
    this.LOAD_HLS_URL().then(() => {
      this.hls_url = this.getHlsUrl;
    });

    this.updateMessages();
  },
  methods: {
    ...mapActions(["LOAD_HLS_URL", "LOAD_MESSAGES"]),
    updateHlsUrl() {
      FB.updateHlsUrl("hls_url", this.hls_url);
    },
    updateMessages() {
      this.computed_messages = []
      this.LOAD_MESSAGES().then(() => {
        this.messages.map(item => {
          const str_date = new Date(item.time).toLocaleString("ko-KR");
          const str_type = item.type || "MESSAGE";

          const msg = {
            ...item,
            date: str_date,
            type: str_type
          };
          this.computed_messages.push(msg);
        });
      });
    },
  }
};
</script>

