<template>
  <div ref="area">
    <v-tabs v-model="tab" background-color="primary" dark class="tab_menu">
      <v-tab v-for="item in this.tab_items" :key="item.tab">
        {{ item }}
      </v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab" id="tab_contents">
      <v-tab-item v-for="item in this.tab_items" :key="item">
        <div class="tabs">
          <Chatting
            v-if="item === 'Chatting'"
            :props_height="contents_height"
          />
          <Emoji v-else :props_height="contents_height" />
        </div>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import Emoji from "@/components/Emoji.vue";
import Chatting from "@/components/Chatting.vue";
import { mapState } from "vuex";
import { EventBus } from "../utils/events-bus";

export default {
  data() {
    return {
      contents_height: 0,
      tab: 0
    };
  },
  computed: {
    ...mapState(["tab_items"])
  },
  components: {
    Chatting,
    Emoji
  },
  mounted() {
    const h = this.$refs.area.getBoundingClientRect().height;
    this.contents_height = h;
  },
  watch: {
    tab: function() {
      EventBus.$emit("selectTab", this.tab);
      console.log("Tab Chaged", this.tab);
    }
  }
};
</script>

<style scoped>
.tab_menu {
  display: none;
}

.tabs {
  z-index: 2;
}

.indicator {
  background-color: transparent;
  width: 100%;
  margin: 0;
  z-index: 1;
}
</style>