import Vue from "vue";
import Vuex from "vuex";
import * as FB from "../api/firebase";
import { EventBus } from '../utils/events-bus'

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    dic_list: [],
    tab_items: ["Chatting", "Emoticon"],
    hrl_url: '',
    message_timer: null,
    last_timestamp: 0,
    last_emoji_timestamp: 0,
    messages: [],
    user_name: '',
    emoji_list: ["😍", "🥳", "✌🏻", "😺", "🤡", "👍🏻", "💋", "⭐️", "🌹"]
  },
  getters: {
    getMessages(state) {
      return state.messages
    },
    getHlsUrl(state) {
      return state.hrl_url
    },
  },
  mutations: {
    ADD_MESSAGES(state, payload) {
      state.messages = state.messages.concat(payload)
    },
    SET_NAME(state) {
      if (!state.user_name) {
        state.user_name = Math.random()
          .toString(36)
          .substring(7);
      }
    },
    SET_HLS_URL(state, payload) {
      state.hrl_url = payload
    }
  },
  actions: {
    LOAD_MESSAGES({ state, commit }) {
      return FB.loadPreviousMessages().then(function (snapshot) {
        const list = []
        snapshot.forEach(function (child) {
          state.dic_list.push(child.key)
          const data = child.val()
          state.last_timestamp = data.time

          const type = data.type || "MESSAGE"
          if (type === "MESSAGE") {
            if (isMine) {
              data.isMine = true
            }

            data.index = child.key
            const isMine = data.user === state.user_name
            list.push(data)
          }
        })

        commit('ADD_MESSAGES', list)
        return snapshot
      })
    },
    SET_MESSAGE_EVENT({ state, commit }) {
      state.message_timer = setInterval(async function () {
        FB.checkMessages(state.last_timestamp).then(function (snapshot) {

          let last_obj = null
          const list = []
          snapshot.forEach(function (child) {
            const data = child.val()
            last_obj = data

            const isExist = state.dic_list.indexOf(child.key) != -1
            // let isExist = false
            // for (let i = state.messages.length - 1; i >= 0; i--) {
            //   const exists = state.messages[i].index === child.key
            //   if (exists) {
            //     isExist = true
            //     break
            //   }
            // }

            if (!isExist) {
              const type = data.type || "MESSAGE"
              if (type === "MESSAGE") {
                data.index = child.key
                const isMine = data.user === state.user_name

                if (!isMine)
                  list.push(child.val())
              }
              else {
                EventBus.$emit('RECIEVE_DATA', child.val())
              }
              state.dic_list.push(child.key)
            }
          })

          if (list.length > 0) {
            commit('ADD_MESSAGES', list)
            if (last_obj == true) state.last_timestamp = last_obj.time
          }
        })
      }, 1000)
    },
    DELETE_MESSAGE_EVENT(state) {
      clearTimeout(state.message_timer)
      state.message_timer = null
    },
    SEND_MESSAGE({ commit, state }, payload) {
      const new_message = {
        type: 'MESSAGE',
        user: state.user_name,
        time: new Date().getTime(),
        message: payload
      }

      FB.addData('messages', new_message).then((error) => {
        console.log('SEND_RESULT', error)
        if (!error) {
          commit('ADD_MESSAGES', {
            ...new_message,
            isMine: true
          })
        }
        return error
      })
    },
    async LOAD_HLS_URL({ commit }) {
      const url = await FB.loadHlsUrl()
      commit('SET_HLS_URL', url)
    },
    ADD_EMOJI({ state }, payload) {
      const new_emoji = {
        type: 'EMOJI',
        user: state.user_name,
        time: new Date().getTime(),
        message: payload
      }

      FB.addData('messages', new_emoji).then((error) => {
        console.log('SEND_EMOJI_RESULT', error)
        return error
      })
    }
  },
  modules: {}
});
