<template>
  <div class="root">
    <div
      id="hid"
      :style="{ left: getLeft, transition: `all ${getDuration}s linear` }"
      :class="{ anim: this.isAnim }"
      class="icon"
    >
      {{ props_data }}
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isAnim: false,
      getLeft: "0px",
      getDuration: 0
    };
  },
  props: ["props_data"],
  mounted() {
    const min_time = 0.8;
    const max_time = 3;
    const time = this.getRandom(min_time, max_time);
    this.getDuration = time;
    const left = Math.floor(this.getRandom(50, window.innerWidth - 50)) + "px";
    this.getLeft = left;
    const timeout = setTimeout(() => {
      clearTimeout(timeout);
      this.$emit("end_anim", this.$vnode.data.ref);
    }, time * 1000);
    const t = setTimeout(() => {
      this.isAnim = true;
      clearTimeout(t);
    }, 10);
  },
  methods: {
    getRandom(min, max) {
      return Math.random() * (max - min) + min;
    }
  }
};
</script>

<style scoped>
.root {
  pointer-events: none;
  z-index: 1;
  position: fixed;
  height: 100%;
  width: 100%;
  /* background-color: #ffe8cc40; */
}
.icon {
  font-size: 30px;
  color: red;
  z-index: 1;
  position: fixed;
  bottom: 0;
  opacity: 1;
  /* transition: all 8s linear; */
}
.anim {
  opacity: 0.2;
  transform: translate(0, -100vh);
}
</style>