<template>
  <div class="outline">
    <form class="form">
      <input
        type="text"
        class="text_input"
        :value="input_data"
        @input="updataInput"
        @keyup.enter="sendMessage"
      />
    </form>
    <v-btn
      icon
      :style="this.isEdit == true && { color: 'white' }"
      :class="{ send_btn: this.isEdit == true, wait_btn: this.isEdit == false }"
      ref="btn"
      @click="sendMessage"
    >
      <v-icon>mdi-arrow-up</v-icon>
    </v-btn>
  </div>
</template>

<script>
import { EventBus } from "../utils/events-bus";

export default {
  data() {
    return {
      input_data: "",
      isEdit: false
    };
  },
  methods: {
    sendMessage() {
      if (!this.input_data) return;

      const message = this.input_data;
      this.$store.dispatch("SEND_MESSAGE", message).then(error => {
        console.log("RR = ", error);

        if (!error) {
          this.input_data = "";
          this.isEdit = false;
          EventBus.$emit("updateList");
        }
      });
    },
    getBtnClass() {
      return this.$refs.input.value == true ? "send_btn" : "wait_btn";
    },
    updataInput(event) {
      this.input_data = event.target.value;
      this.isEdit = this.input_data.length > 0;
    }
  }
};
</script>

<style scoped>
.outline {
  background-color: white;
  display: flex;
}

.form {
  flex: 1;
}

.text_input {
  width: 100%;
  height: 100%;
  padding-left: 10px;
  padding-right: 10px;
  font-family: "Nanum Gothic", sans-serif;
}

input:focus,
input.form-control:focus {
  outline: none !important;
  outline-width: 0 !important;
  box-shadow: none;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
}

.send_btn {
  background: #3269cd;
  margin: 6px;
}

.wait_btn {
  background: transparent;
  margin: 6px;
}
</style>