<template>
  <div id="root" class="contents_area" :style="contents_style">
    <div class="count">{{ h_count }}</div>
    <component
      class="user_none"
      v-for="(item, key) in obj"
      :key="key"
      :ref="key"
      :is="item.component"
      @end_anim="deleteItem"
      :props_data="item.emoji"
    >
    </component>
    <VideoView />
    <MenuBar class="menu" />
    <Tabs class="item" />
  </div>
</template>

<script>
import VideoView from "@/components/VideoView.vue";
import MenuBar from "@/components/MenuBar.vue";
import Tabs from "@/components/Tabs.vue";
import { EventBus } from "../utils/events-bus";
import EmojiIcon from "@/components/EmojiIcon.vue";

export default {
  name: "App",

  components: {
    VideoView,
    MenuBar,
    Tabs,
    EmojiIcon
  },

  data: () => ({
    h_count: 0,
    obj: {},
    contents_style: {
      height: document.documentElement.clientHeight + "px"
    }
    //
  }),
  created() {
    EventBus.$on("RECIEVE_DATA", data => {
      this.obj["idx" + this.h_count] = {
        component: EmojiIcon,
        emoji: data.message
      };
      this.h_count++;
    });
  },
  mounted() {
    this.$store.commit("SET_NAME");
  },
  methods: {
    getEmoji(index) {
      this.emoji_list[index];
    },
    deleteItem(payload) {
      this.$delete(this.obj, payload);
    }
  }
};
</script>

<style scoped>
.contents_area {
  display: flex;
  flex-direction: column;
}

.menu {
  z-index: 1;
}

.item {
  z-index: 0;
  flex: 1;
}

.count {
  display: none;
}

.user_none {
  pointer-events: none;  
}
</style>
